import React, { useEffect, useState } from "react";
import PopupWrapper from "../FinanceManager/FeesStructure/PopupWrapper";
import BorderBottom from "../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";
import JoinFormTextArea from "../../../../JoiningForm/Student/Form/JoinFormTextArea";
import QLoader from "../../../../Loader/QLoader";
import style from "../DepartmentHead/Exminations/SeatingArrangement/SeatingArrangement.module.css";
import { useTranslation } from "react-i18next";
import JoinFormInput from "../../../../JoiningForm/Student/Form/JoinFormInput";
import FormToggle from "../../../../StaffTab/components/DividerTab/MainContent/StaffForm/FormToggle";
import StaffMentorMarkAttendance from "./StaffMentorMarkAttendance";
import {
  useAddMeetingByMentor,
  useAddMeetingScheduleByMentor,
  useAddMeetingByMentorEdit,
} from "../../../../hooks/member_tab/department-api";
import JoinFormCalender from "../../../../JoiningForm/Student/Form/JoinFormCalender";
const StaffMentorAddMeeting = ({
  onRefetch,
  onClose,
  mid,
  type,
  meetingDetail,
}) => {
  const { t } = useTranslation();
  const [filled, setFilled] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [mark, setMark] = useState(false);
  const [state, setState] = useState({
    agenda: "",
    discussion: "",
    summary: "",
    meeting_alert: true,
    meeting_time: "",
    meeting_date: "",
  });

  const [addMeetingByMentor] = useAddMeetingByMentor();
  const [addMeetingByMentorEdit] = useAddMeetingByMentorEdit();
  const [addMeetingScheduleByMentor] = useAddMeetingScheduleByMentor();

  useEffect(() => {
    if (type === "EDIT") {
      setState((prev) => ({
        ...prev,
        agenda: meetingDetail?.agenda ?? "",
        discussion: meetingDetail?.discussion ?? "",
        summary: meetingDetail?.summary ?? "",
        meeting_alert: meetingDetail?.meeting_alert ?? false,
        meeting_time: meetingDetail?.meeting_time ?? "",
        meeting_date: meetingDetail?.meeting_date ?? "",
      }));
    }
  }, [type, meetingDetail]);
  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onValidation = (data) => {
    const errors = {};
    for (let obj in data) {
      if (!data[obj]) errors[obj] = `${obj} is required!`;
    }
    return errors;
  };
  const onMark = () => {
    setMark((pre) => !pre);
  };

  const timeFormatTable = (vale) => {
    const splitValue = vale?.split(":");
    let meridian = "";
    if (splitValue[0] > 12) {
      meridian = "Pm";
      splitValue[0] -= 12;
    } else if (splitValue[0] < 12) {
      meridian = "Am";
      if (splitValue[0] === 0) {
        splitValue[0] = 12;
      }
    } else {
      meridian = "Pm";
    }
    const hour = splitValue[0] > 9 ? splitValue[0] : `0${+splitValue[0]}`;
    return `${hour}:${splitValue[1]} ${meridian}`;
  };

  const onAddMeeting = (data) => {
    if (type === "EDIT") {
      if (meetingDetail?._id) {
        setDisabled((pre) => !pre);

        addMeetingByMentorEdit({
          meid: meetingDetail?._id,
          meetingData: {
            ...state,
            meid: meetingDetail?._id,
            p_array: data?.present,
            a_array: data?.absent,
            meeting_time: timeFormatTable(state.meeting_time),
          },
        })
          .then(() => {
            onRefetch();
            onClose();
          })
          .catch({});
      }
    } else {
      if (mid) {
        setDisabled((pre) => !pre);

        addMeetingByMentor({
          meetingData: {
            ...state,
            mid: mid,
            p_array: data?.present,
            a_array: data?.absent,
          },
        })
          .then(() => {
            onRefetch();
            onClose();
          })
          .catch({});
      }
    }
  };
  const onDateFunctionFrom = (val) => {
    let splitDate = val?.split("/");
    let modify = `${splitDate[2]}-${splitDate[1]}-${splitDate[0]}`;
    setState((prev) => ({
      ...prev,
      meeting_date: modify,
    }));
  };
  const onAddMeetingSchedule = (data) => {
    if (mid) {
      setDisabled((pre) => !pre);

      addMeetingScheduleByMentor({
        meetingData: {
          ...state,
          mid: mid,
          p_array: [],
          a_array: [],
          meeting_time: timeFormatTable(state.meeting_time),
        },
      })
        .then(() => {
          onRefetch();
          onClose();
        })
        .catch({});
    }
  };
  return (
    <PopupWrapper onClose={onClose}>
      <div
        className={style.seating_modal}
        style={{
          width: "40rem",
        }}
      >
        {!mark ? (
          <>
            <section
              className={style.seating_modal_title_left}
              style={{
                position: "relative",
              }}
            >
              <img
                src="/images/arrow-left-fees-icon.svg"
                onClick={onClose}
                alt=""
              />
              <h6>{t("add_meeting")}</h6>
            </section>
            <BorderBottom
              customStyle={{
                width: "100%",
                marginBottom: "1rem",
              }}
            />
            <FormToggle
              labelHeading={t("add_meeting_alert")}
              labelDescription=""
              defaultLabel={false}
              customContainer={{
                padding: "0",
              }}
              checkedToggle={state.meeting_alert}
              onToggleHandler={(e) =>
                setState((prev) => ({
                  ...prev,
                  meeting_alert: e.target.checked,
                }))
              }
            />
            <JoinFormInput
              labelText={t("agenda")}
              placeholder={t("agenda_placeholder")}
              name={"agenda"}
              value={state.agenda}
              type={"text"}
              onChange={onInputChange}
              errorShow={filled["agenda"]}
            />

            {type === "SCHEDULE" ? (
              <>
                <JoinFormCalender
                  labelText={t("date")}
                  placeholder={t("select_date")}
                  name={"date"}
                  value={state.meeting_date}
                  onDateFunction={onDateFunctionFrom}
                  customStyleContainer={{ position: "relative" }}
                  datePickerPosition={{
                    top: "-8rem",
                  }}
                />
                <JoinFormInput
                  labelText={t("time")}
                  placeholder="00:00"
                  name={"meeting_time"}
                  value={state?.meeting_time}
                  type={"time"}
                  onChange={(e) =>
                    setState((prev) => ({
                      ...prev,
                      meeting_time: e.target.value,
                    }))
                  }
                  errorShow={filled["meeting_time"]}
                />
                <button
                  className={style.start_arrangement_btn}
                  onClick={onAddMeetingSchedule}
                >
                  {t("save")}
                </button>
              </>
            ) : (
              <>
                <JoinFormTextArea
                  labelText={t("summary")}
                  placeholder={t("summary_placeholder")}
                  name={"summary"}
                  value={state.summary}
                  type={"text"}
                  onChange={onInputChange}
                  customStyleContainer={{
                    width: "100%",
                  }}
                  errorShow={filled["summary"]}
                />
                <JoinFormTextArea
                  labelText={t("discussion")}
                  placeholder={t("discussion_placeholder")}
                  name={"discussion"}
                  value={state.discussion}
                  type={"text"}
                  onChange={onInputChange}
                  customStyleContainer={{
                    width: "100%",
                  }}
                  errorShow={filled["discussion"]}
                />
                <button
                  className={style.start_arrangement_btn}
                  onClick={onMark}
                >
                  {t("mark_attendance")}
                </button>
              </>
            )}

            {/* <button
              className={style.start_arrangement_btn}
              onClick={onAddMeeting}
            >
              {t("add")}
            </button> */}
          </>
        ) : (
          <StaffMentorMarkAttendance
            mid={mid}
            onClose={onMark}
            onAddMeeting={onAddMeeting}
          />
        )}

        {disabled && <QLoader />}
      </div>
    </PopupWrapper>
  );
};

export default StaffMentorAddMeeting;
