import React, { useState } from "react";
import moment from "moment";
import style from "../Mentor.module.css";
import MentorTabChangeLink from "../../../StaffMentor/MentorTabChangeLink";
import { Link, useParams } from "react-router-dom";
import { assestsUserFinanceUrl } from "../../../../../../optimized/Services/UrlConfig/AssestsBaseUrl";
import StaffMentorAddMeeting from "../../../StaffMentor/StaffMentorAddMeeting";
const MeetingCard = ({
  meeting_detail,
  carryParentState,
  queryFlow,
  onRefetch,
  mid,
}) => {
  const params = useParams();

  const [open, setOpen] = useState(false);
  return (
    <>
      {queryFlow === "By_Mentor" ? (
        <>
          {meeting_detail?.creation_status === "SCHEDULE" ? (
            <div
              className={style.mentor_card_container}
              style={{
                marginBottom: "0.7rem",
                justifyContent: "space-between",
              }}
            >
              <div className={style.mentor_card_int}>
                <img
                  src="/images/member_tab/student/student-query-icon.svg"
                  style={{
                    borderRadius: "50%",
                  }}
                  alt="mentor avatar"
                />
                <div
                  className={style.mentor_container_inner}
                  style={{
                    gap: "0.6rem",
                  }}
                >
                  <h6>{meeting_detail?.agenda ?? ""}</h6>
                  <p>
                    {moment(meeting_detail?.meeting_date).format("DD MMM yyyy")}
                    ,{meeting_detail?.meeting_time}
                  </p>
                </div>
              </div>

              <img
                src={`${assestsUserFinanceUrl}/menu.svg`}
                alt="menu icon"
                style={{
                  width: "1.5rem",
                  height: "1.5rem",
                  cursor: "pointer",
                }}
                onClick={() => setOpen(true)}
              />
            </div>
          ) : (
            <MentorTabChangeLink
              carryParentState={{
                ...carryParentState,
                meid: meeting_detail?._id,
              }}
              activeTab={"meeting-detail"}
            >
              <div
                className={style.mentor_card_container}
                style={{
                  marginBottom: "0.7rem",
                }}
              >
                <img
                  src="/images/member_tab/student/student-query-icon.svg"
                  style={{
                    borderRadius: "50%",
                  }}
                  alt="mentor avatar"
                />
                <div
                  className={style.mentor_container_inner}
                  style={{
                    gap: "0.6rem",
                  }}
                >
                  <h6>{meeting_detail?.agenda ?? ""}</h6>
                  <p>
                    {moment(meeting_detail?.created_at).format("DD MMM yyyy")},
                    {moment(meeting_detail?.created_at).format("LT")}
                  </p>
                </div>
              </div>
            </MentorTabChangeLink>
          )}
        </>
      ) : (
        <Link
          to={`/q/${params.username}/member/student/class?a=mentor&n=meeting-detail`}
          state={{
            ...carryParentState,
            meid: meeting_detail?._id,
          }}
          activeTab={"mentor&n=queries&query"}
        >
          <div
            className={style.mentor_card_container}
            style={{
              marginBottom: "0.7rem",
            }}
          >
            <img
              src="/images/member_tab/student/student-query-icon.svg"
              style={{
                borderRadius: "50%",
              }}
              alt="mentor avatar"
            />
            <div
              className={style.mentor_container_inner}
              style={{
                gap: "0.6rem",
              }}
            >
              <h6>{meeting_detail?.agenda ?? ""}</h6>
              <p>
                {moment(meeting_detail?.created_at).format("DD MMM yyyy")},
                {moment(meeting_detail?.created_at).format("LT")}
              </p>
            </div>
          </div>
        </Link>
      )}
      {open && (
        <StaffMentorAddMeeting
          onClose={() => setOpen(false)}
          mid={mid}
          onRefetch={onRefetch}
          type="EDIT"
          meetingDetail={meeting_detail}
        />
      )}
    </>
  );
};

export default MeetingCard;
