import { useTranslation } from "react-i18next";
import ModalMenu from "../../../Ui/Modal/ModalMenu";

const AddMeetingConfig = ({ onClose, onStep }) => {
  const { t } = useTranslation();
  return (
    <ModalMenu onClose={onClose}>
      <h6
        onClick={() => {
          onStep("RECORD");
          onClose();
        }}
      >
        {t("record_meeting")}
      </h6>
      <h6
        onClick={() => {
          onStep("SCHEDULE");
          onClose();
        }}
      >
        {t("schedule_meeting")}
      </h6>
    </ModalMenu>
  );
};

export default AddMeetingConfig;
